html {
	scroll-behavior: smooth;
}
.raleway-normal{
	font-family: "Raleway", sans-serif;
  	font-optical-sizing: auto;
  	font-style: normal;
  	font-weight: 400;
}
.App {
	width: 100vw;
	display: flex;
	flex-direction: column;
	align-items: center;
	position: relative;
	min-height: 100vh;
	justify-content: center;
}
.content {
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 90%;
  	max-width: 1200px;
	z-index: 1;
	margin-top: 15px;
}
.logo {
	width: 30%;
	max-width: 300px;
	min-width: 180px;
	margin-left: 40px;
	margin-top: 10px;
}
.title {
	text-align: center;
	margin: 20px;
	font-family: "Raleway", sans-serif;
	font-optical-sizing: auto;
	font-style: normal;
	font-weight: bold;
}
.container {
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;
	width: 100%;
}
.navigateBtns {
	width: 95%;
	display: flex;
	justify-content: space-between;
	align-items: center;
	z-index: 1;
}
.btn{
  border: none;
	padding: 10px;
	margin-bottom: 20px;
	background-color: #66C2C9;
	color: white;
	font-weight: bolder;
	font-size: 15px;
	transition: all .2s ease-in-out;
	cursor: pointer;
}
.btn:hover{
  background-color: black;
}
.backBtn{

}
.nextBtn{

}
.inputsBox {
	width: 90%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}
.input-wrapper {
