}

::-webkit-scrollbar-thumb {
    border-radius: 5px;
    background-color: rgba(0, 0, 0, 0.5);
    /* background-color: #a0ebf287; */
    /* background-color: #FDCA40; */
    -webkit-box-shadow: 0 0 1px rgba(255, 255, 255, 0.5);
}

/* For Internet Explorer and Edge */
